    left: 0;
    top: -25px;
    position: absolute;
    z-index: 1; }
  .bottom-content__wrapper {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center; }
  .bottom-content .btn-secondary {
    margin-top: -50px;
    z-index: 2; }
    .bottom-content .btn-secondary span {
      position: relative;
      z-index: 2; }

